import { INVOICE_TYPE } from '../constants';

export const getInvoiceType = (type) => {
	switch (type) {
		case INVOICE_TYPE.CONTRIBUTION:
			return '';
		case INVOICE_TYPE.CASH:
			return ' ';
		case INVOICE_TYPE.DEBIT_CARD:
			return ' ';
		case INVOICE_TYPE.CREDIT_CARD:
			return ' ';
		default:
		//Null
	}
};
